import React, { useEffect, useState } from 'react';
import { delay } from '../../../helpers/helpers';
import SpinnerCenter from '../../../Views/SpinnerWrapper/SpinnerCenter';
import Spinner from '../../../Views/SpinnerWrapper/Spinner';
import style from './styles/infoBlock.module.scss';

const NoProducts = ({
    children = '',
    text = 'Поиск ...'
}) => {
    const [dataText, setDataText] = useState(<SpinnerCenter>
        <Spinner />
    </SpinnerCenter>);
    useEffect(() => {
        const asyncFunc = async () => {
            setDataText(
                <div
                    className={style['info-block__about-products']}
                >
                    {text}
                </div>
            );
            await delay(5000)
            setDataText(
                <div
                    className={style['info-block__about-products']}
                >
                    {children}
                </div>
            );
        }
        asyncFunc()
    }, [])
    return (
        <React.Fragment>
            {
                dataText ?
                    dataText
                    : null
            }
        </React.Fragment>

    )
}

export default NoProducts;